<template>
	<div class="page-list">
		<div class="workpanel-head">
			<my-breadcrumb level1="" level2=""></my-breadcrumb>
			<button class="mybtn green-btn-lg page-btn" id="toAdd" @click="toAdd">
				<img src="../../assets/images/add.png"/>&nbsp;
			</button>
			<my-searchbox ref="searchparam" @search="search" placeholder=""></my-searchbox>
		</div>	   
		<div class="workpanel-main">
			<div id="userListDiv">
				<el-table v-loading="loading" :data="userlist" :height="tHeight" style="width: 100%;">
					<el-table-column label="#" width="80" align="center" type="index"></el-table-column>
		    		<el-table-column prop="id" label=""></el-table-column>
		    		<el-table-column prop="userName" label=""></el-table-column>
		    		<el-table-column prop="gender" label="" align="center" :formatter="formatGender"></el-table-column>
		    		<el-table-column prop="phone" label=""></el-table-column>
		    		<el-table-column prop="email" label=""></el-table-column>
		    		<el-table-column prop="state" label="" align="center" :formatter="formatState"></el-table-column>
		    		<el-table-column label="" align="center">
		    			<template slot-scope="scope">
	        				<el-button @click="toEdit(scope.row)" type="text" size="small"></el-button>
	        				<el-button @click="resetPwd(scope.row)" type="text" size="small"></el-button>
	        				<el-button @click="changeState(scope.row)" type="text" size="small"><span v-if="scope.row.state=='Y'"></span><span v-else="scope.row.state=='N'"></span></el-button>
	      				</template>
		    		</el-table-column>
		  		</el-table>
	  		</div>
		</div>
		<div class="workpanel-foot">
			<el-pagination 
      			@current-change="handleCurrentChange"
      			:current-page.sync="currentPage"
			    :page-size="pageSize"
			    layout="prev, pager, next, total"
			    :total="totalCount">
    		</el-pagination>
		</div>
	</div>
</template>

<script>
import Breadcrumb from '@/components/tool/Breadcrumb'
import Searchbox from '@/components/tool/Searchbox'
import axios from 'axios'
import $ from 'jquery'

export default{
	data(){
		return{
			searchName: "",
			userlist: [],
			currentPage: 1,
			pageSize: 10,
			totalCount: 0,
			axiosParam:{
				'userName': '',
				'currentPage': 1
			},
			loading: true,
			tHeight: 0 // 
		}
	},
	mounted:function(){
		//div
		$("#userListDiv").height($(".workpanel-main").height());
		this.tHeight = $(".workpanel-main").height();

		//
		this.getList(this.axiosParam);		
	},	
	methods:{
		//
		getList: function(param){
			var page = this; 
			page.loading = true;			
			axios.post('/web/user/index.do',param).then(function (response) {
				page.loading = false;
			    if(response.data.success){
			    	page.userlist = response.data.bean.data;
			    	page.totalCount = response.data.bean.rowCount;			    	
			    }else{
			    	page.$message.error(response.data.message);
			    }
			}).catch(function (error) {
				console.log(error);
				page.loading = false;
				page.$message.error("");
			});
		},
		//
		search: function(){		
			this.currentPage = 1;
			this.axiosParam = {
				'userName': $.trim(this.$refs.searchparam.param),
				'currentPage': this.currentPage
			};
			this.getList(this.axiosParam);
		},
		//
		toAdd: function(){
			this.$router.push({ path: '/main/user/add' });
		},
		//
		handleCurrentChange(val) {
			this.axiosParam.currentPage = val;
       		this.getList(this.axiosParam);
      	},
      	//
      	formatGender: function(row, column){
      		if(row.gender=="1"){
      			return "";
      		}else if(row.gender=="2"){
      			return "";
      		}
      	},
      	//
      	formatState: function(row, column){
      		if(row.state=="Y"){
      			return "";
      		}else if(row.state=="N"){
      			return "";
      		}
      	},
      	//
      	toEdit: function(user){
      		this.$router.push({ path: '/main/user/add' ,query: {edit:'1',userId:user.id}});
      	},
      	//
      	resetPwd: function(user){
      		var page = this;
      		this.$confirm(' '+user.userName+' \"123456\"', '', {
          		confirmButtonText: '',
          		cancelButtonText: '',
          		type: 'warning'
        	}).then(() => {
          		axios.post('/web/user/changePass.do',{
    				'userId': user.id
  				}).then(function (response) {
				    page.loading = false;
				    if(response.data.success){
				    	page.$message({
          					message: '',
          					type: 'success'
        				});
				    }else{
				    	page.$message.error(response.data.message);
				    }
 				}).catch(function (error) {
    				console.log(error);
    				page.loading = false;
    				page.$message.error("");
				});
        	}).catch(() => {});
      	},
      	///
      	changeState: function(user){
      		var opera = "";
      		var stat = "";
      		if(user.state=="Y"){
      			opera = "";
      			stat = "N";
      		}else if(user.state=="N"){
      			opera = "";
      			stat = "Y";
      		}
      		var page = this;
      		this.$confirm(''+opera+' '+user.userName+'', '', {
          		confirmButtonText: '',
          		cancelButtonText: '',
          		type: 'warning'
        	}).then(() => {
        		page.loading = true;
          		axios.post('/web/user/editUser.do',{
					'id': user.id,
					'state': stat
				}).then(function (response) {
				    page.loading = false;
				    user.state = stat;
				    if(response.data.success){
				    	page.$message({
	      					message: opera+'',
	      					type: 'success'
	    				});
				    }else{
				    	page.$message.error(response.data.message);
				    }
				}).catch(function (error) {
					console.log(error);
					page.loading = false;
					page.$message.error("");
				});
        	}).catch(() => {});
      	}	
	}
}
</script>




