<template>
  	<div class="page-list">
   		<div class="workpanel-head">
			<my-breadcrumb :level1="info1" :level2="info2"></my-breadcrumb>
		</div>	   
		<div class="workpanel-main-add" v-loading="loading">
			<div class="add-top"></div>
			<br/>
			<div class="my-form">
				<el-form :model="webForm" :rules="rules" ref="webForm" label-width="100px" class="demo-webForm">
					<div class="form-title"><span class="redstar"> *</span></div>
				  	<el-form-item label="" prop="webName">
				    	<el-input v-model="webForm.webName" auto-complete="off" placeholder="" :maxlength="maxlength100"></el-input>
				  	</el-form-item>

				  	<div class="form-title">URL<span class="redstar"> *</span>
				  		<span class="remark">http://https://</span>
				  	</div>
				  	<el-form-item label="" prop="webUrl">
				    	<el-input v-model="webForm.webUrl" auto-complete="off" placeholder="URL" :maxlength="maxlength400"></el-input>
				  	</el-form-item>

				  	<div class="form-title"><span class="redstar"> *</span>
				  		<span class="remark"><span v-if="entrance=='key'"></span><span v-else="entrance!='key'"></span></span>
				  	</div>
				  	<el-form-item label="" prop="webInterval">
				    	<el-input v-model="webForm.webInterval" auto-complete="off" placeholder="" :maxlength="maxlength11"></el-input>
				  	</el-form-item>

					<div v-show="entrance!='web'">
					  	<div class="form-title"></div>
					  	<el-form-item label="" prop="linkType">
	    					<el-radio-group v-model="webForm.linkType">
	      						<el-radio label="1"></el-radio>
	      						<el-radio label="2"></el-radio>
	    					</el-radio-group>
	  					</el-form-item>

					  	<div class="form-title"><span class="redstar"> *</span></div>
					  	<el-form-item label="" prop="receiver">
	    					<el-select v-model="webForm.receiver" placeholder="">
	      						<el-option v-for="rec in recs" :key="rec.id" :label="rec.userName" :value="rec.id">
	      							<span style="float: left">{{ rec.userName }}</span>
	    						</el-option>
	    					</el-select>
	  					</el-form-item>
  					</div>

  					<div class="form-title"></div>
				  	<el-form-item label="" prop="state">
    					<el-radio-group v-model="webForm.state">
      						<el-radio label="Y"></el-radio>
      						<el-radio label="N"></el-radio>
    					</el-radio-group>
  					</el-form-item>
  					<br/>
				  	<button class="mybtn blue-btn-sm saveu-btn" @click="saveInfo"></button>
				  	<button class="mybtn white-btn-sm canelu-btn" @click="back"></button>
				  	<br/><br/>
				</el-form>
			</div>
		</div>
  	</div>
</template>

<script>
import Breadcrumb from '@/components/tool/Breadcrumb'
import axios from 'axios'
import $ from 'jquery'

export default{
	data(){
		//url
		var validateUrl = (rule, value, callback) => {
        	if (value === '') {
          		callback(new Error('URL'));
        	} else {
        		var strRegex = '^((https|http|ftp|rtsp|mms)?://)'
					+ '?(([0-9a-z_!~*\'().&=+$%-]+: )?[0-9a-z_!~*\'().&=+$%-]+@)?' //ftpuser@ 
					+ '(([0-9]{1,3}.){3}[0-9]{1,3}' // IPURL- 199.194.52.184 
					+ '|' // IPDOMAIN 
					+ '([0-9a-z_!~*\'()-]+.)*' // - www. 
					+ '([0-9a-z][0-9a-z-]{0,61})?[0-9a-z].' //  
					+ '[a-z]{2,6})' // first level domain- .com or .museum 
					+ '(:[0-9]{1,4})?' // - :80 
					+ '((/?)|' // a slash isn't required if there is no file name 
					+ '(/[0-9a-z_!~*\'().;?:@&=+$,%#-]+)+/?)$'; 
				var urlReg=new RegExp(strRegex); 
          		if(urlReg.test(value)){
          			callback();
          		}else{
          			callback(new Error('URL'));
          		}
        	}
      	};
      	//
      	var validateReceiver = (rule, value, callback) => {
        	if (value === '' && this.entrance!='web') {
          		callback(new Error(''));
        	} else {
        		callback();
        	}
      	};
      	var validateNum = (rule, value, callback) => {
        	if (value === '') {
          		callback(new Error(''));
        	} else {
        		var numReg = /^\d+$/;
          		if(numReg.test(value)){
          			if(value==0 || value=="0"){
          				callback(new Error('0'));
          			}else{
          				callback();
          			}        			
          		}else{
          			callback(new Error(''));
          		}
        	}
      	};
		return{
			maxlength100: 100,
			maxlength400: 400,
			maxlength11: 11,
			entrance: '',  //keywebkeyupdate
			mode: '',   //  add/edit
			info1: '',  //
			info2: '',  //
			recs: [],  //
			type: '',  //123
			webForm: {				
	          	webName: '',
	          	webUrl: '',
	          	webInterval: '',
	          	linkType: '1',
	          	receiver: '',
	          	state: 'N'	          	
	        },
	        rules: {
	          	webName: [
	            	{ required: true, message: '', trigger: 'blur' }
	          	],
	          	webUrl: [
	            	{ validator: validateUrl, trigger: 'blur' }
	          	],
	          	webInterval: [
	            	{ validator: validateNum, trigger: 'blur' }
	          	],
	          	receiver: [
	            	{ validator: validateReceiver, trigger: 'change' }
	          	],
	        },
	        loading: true
		}
	},
	mounted: function(){
		this.entrance = this.$route.query.entrance;
		if(this.entrance=="key"){
			this.info1 = "";
			this.type = "1";
		}else if(this.entrance=="web"){
			this.info1 = "";
			this.type = "2";
		}else{
			this.info1 = "";
			this.type = "3";
		}

		var page = this;
		//
		axios.post('/web/user/findAll.do').then(function (response) {
		    if(response.data.success){		    	
		    	page.recs = response.data.bean;	    	

		    	//
		    	var edit = page.$route.query.edit;		
				if(edit=="1"){
					page.mode = 'edit';
					page.info2 = ' / ';
					//id
					axios.post('/web/web/findWeball.do',{
						'webId': page.$route.query.webId				
					}).then(function (response) {
					    page.loading = false;
					    if(response.data.success){
					    	var w = response.data.bean;
					    	page.webForm.webName = w.webName;
					    	page.webForm.webUrl = w.webUrl;
					    	page.webForm.webInterval = w.webInterval;
					    	page.webForm.linkType = w.linkType;
					    	page.webForm.receiver = w.userId;
					    	page.webForm.state = w.state;					    	
					    }else{
					    	page.$message.error(response.data.message);
					    }
					}).catch(function (error) {
						console.log(error);
						page.loading = false;
						page.$message.error("");
					});
				}else{
					page.loading = false;
					page.mode = 'add';
					page.info2 = ' / ';
				}
		    }else{
		    	page.loading = false;
		    	console.log(response.data.message);
		    }
		}).catch(function (error) {
			console.log(error);
			page.loading = false;
			page.$message.error("");
		});
	},
	methods:{
		//
		saveInfo: function(){
			var page = this;
			var url = "/web/web/addWeb.do";
			if(this.mode=="edit"){
				url = "/web/web/editWeb.do";
			}

			page.$refs['webForm'].validate((valid) => {
          		if (valid) {
          			page.loading = true;
					axios.post(url,{
						'id': page.$route.query.webId,
						'webName': $.trim(page.webForm.webName),
						'webUrl': $.trim(page.webForm.webUrl),
						'webInterval': $.trim(page.webForm.webInterval),
						'type': this.type,
						'linkType': page.webForm.linkType,
						'state': page.webForm.state,
						'userId': page.webForm.receiver
					}).then(function (response) {
					    page.loading = false;
					    if(response.data.success){
					    	page.$message({
		      					message: '',
		      					type: 'success'
		    				});
		    				page.$router.push({path: '/main/'+page.entrance+'/result',query:{entrance: page.entrance}});
					    }else{
					    	page.$message.error(response.data.message);
					    }
					}).catch(function (error) {
						console.log(error);
						page.loading = false;
						page.$message.error("");
					});
				}
			});
		},
		//
		back: function(){
			history.go(-1);
		}
	}
}
</script>

<style scoped>
.saveu-btn{
	margin-left: 320px;
}
.canelu-btn{
	margin-left: 20px;
}
.el-select,.el-input__inner{
	width: 620px !important;
}
.remark{
	color: #999;
}
</style>


