<template>
	<div class="page-list">
		<div class="workpanel-head">
			<my-breadcrumb :level1="info" level2=""></my-breadcrumb>
			<button class="mybtn green-btn-lg page-btn" id="addweb" @click="addweb">
				<img src="../assets/images/add.png"/>&nbsp;
			</button>

			<my-searchbox ref="searchparam" @search="search" placeholder=""></my-searchbox>			
		</div>	   
		<div class="workpanel-main">
			<div id="resultDiv">
				<el-table v-loading="loading" :data="result" :height="tHeight" style="width: 100%;">
					<el-table-column label="#" width="80" align="center" type="index"></el-table-column>
		    		<el-table-column prop="WEB_NAME" label="" width="280"></el-table-column>
		    		<el-table-column prop="WEB_URL" label="URL" width="280"></el-table-column>
		    		<el-table-column prop="WEB_INTERVAL" label="" align="center" :formatter="formatInterval"></el-table-column>
		    		<el-table-column prop="LINK_TYPE" label="" align="center" :formatter="formatLinkType"></el-table-column>
		    		<el-table-column prop="USER_ID" label="" align="center"></el-table-column>
		    		<el-table-column prop="STATE" label="" align="center" :formatter="formatState"></el-table-column>
		    		<el-table-column label="" align="center">
		    			<template slot-scope="scope">
	        				<el-button @click="toEdit(scope.row)" type="text" size="small"></el-button>
	        				<el-button @click="changeState(scope.row)" type="text" size="small"><span v-if="scope.row.STATE=='Y'"></span><span v-else="scope.row.STATE=='N'"></span></el-button>
	      				</template>
		    		</el-table-column>
		  		</el-table>
	  		</div>
		</div>
		<div class="workpanel-foot">
			<el-pagination 
      			@current-change="handleCurrentChange"
      			:current-page.sync="currentPage"
			    :page-size="pageSize"
			    layout="prev, pager, next, total"
			    :total="totalCount">
    		</el-pagination>
		</div>
	</div>
</template>

<script>
import Breadcrumb from '@/components/tool/Breadcrumb'
import Searchbox from '@/components/tool/Searchbox'
import axios from 'axios'
import $ from 'jquery'

export default{
	data(){
		return{
			entrance: "",    //keywebkeyupdate
			info: "",       //
			type: "",
			result: [],
			currentPage: 1,
			pageSize: 10,
			totalCount: 0,
			axiosParam:{
				'type': '',
				'webName': '',
				'currentPage': 1
			},
			loading: true,
			tHeight: 0 // 
		}
	},
	mounted: function(){
		//div
		$("#resultDiv").height($(".workpanel-main").height());
		this.tHeight = $(".workpanel-main").height();

		this.resetParams();
		this.getList(this.axiosParam);				
	},
	//
  	watch: {
    	'$route' (to, from) {
     		this.watchDifferentEntrance()
   		}
  	},
	methods:{
		watchDifferentEntrance() {
			this.resetParams();
			this.getList(this.axiosParam);	
    	},
    	//
    	resetParams: function(){
    		this.loading = true;
			this.$refs.searchparam.param = "";

    		this.entrance = this.$route.query.entrance; 
			//
			if(this.entrance=="key"){
				this.info = "";
				this.type = "1";
			}else if(this.entrance=="web"){
				this.info = "";
				this.type = "2";
			}else{
				this.info = "";
				this.type = "3";
			}
			this.axiosParam.type = this.type;
    	},
    	//
    	getList: function(param){
    		var page = this;
    		page.loading = true;			 
			axios.post('/web/web/findWeb.do',param).then(function (response) {
				page.loading = false;
			    if(response.data.success){
			    	page.result = response.data.bean.data;	
			    	page.totalCount = response.data.bean.rowCount;
			    }else{
			    	page.$message.error(response.data.message);
			    }
			}).catch(function (error) {
				console.log(error);
				page.loading = false;
				page.$message.error("");
			});
    	},
    	//
		search: function(){
			this.currentPage = 1;
			this.axiosParam = {
				'type': this.type,
				'webName': $.trim(this.$refs.searchparam.param),
				'currentPage': this.currentPage
			};
			this.getList(this.axiosParam);
		},
		addweb: function(){
			this.$router.push({ path: '/main/'+this.entrance+'/add', query: { entrance: this.entrance }});
		},
		//
		handleCurrentChange(val) {
       		this.axiosParam.currentPage = val;
       		this.getList(this.axiosParam);
      	},
      	//
      	toEdit: function(web){
      		this.$router.push({ path: '/main/'+this.entrance+'/add' ,query: { entrance: this.entrance,webId:web.ID,edit:'1'}});
      	}, 
      	///
      	changeState: function(web){
      		var opera = "";
      		var stat = "";
      		if(web.STATE=="Y"){
      			opera = "";
      			stat = "N";
      		}else if(web.STATE=="N"){
      			opera = "";
      			stat = "Y";
      		}
      		var page = this;
      		this.$confirm(''+opera+' '+web.WEB_NAME+'', '', {
          		confirmButtonText: '',
          		cancelButtonText: '',
          		type: 'warning'
        	}).then(() => {
        		page.loading = true;
          		axios.post('/web/web/editWebStatus.do',{
					'id': web.ID,
					'state': stat,
					'type': page.type
				}).then(function (response) {
				    page.loading = false;
				    web.STATE = stat;
				    if(response.data.success){
				    	page.$message({
	      					message: opera+'',
	      					type: 'success'
	    				});
				    }else{
				    	page.$message.error(response.data.message);
				    }
				}).catch(function (error) {
					console.log(error);
					page.loading = false;
					page.$message.error("");
				});
        	}).catch(() => {});
      	},
      	//
      	formatInterval: function(row, column){
      		if(this.entrance=="key"){
      			return row.WEB_INTERVAL +"";
      		}else{
      			return row.WEB_INTERVAL +"";
      		}
      	},
      	//
      	formatLinkType: function(row, column){
      		if(row.LINK_TYPE=="1"){
      			return "";
      		}else{
      			return "";
      		}
      	},
      	//
      	formatState: function(row, column){
      		if(row.STATE=="Y"){
      			return "";
      		}else{
      			return "";
      		}
      	}
	}
}
</script>




